<template>
  <section class="home-blog">
    <div class="container">
      <the-title :theme="theme" :title="title" />
      
      <div class="blog-wrapper">
        <blog-card 
          @click="goToBlogPost(post._id)"
          v-for="post in posts.slice(0, 3)" 
          :key="post._id"
          :post_data="post"
        />
      </div>
      
      <div class="blog-btn">
        <router-link class="link" :to="{ name: 'Blog' }">Show more</router-link>
      </div>
    </div>
  </section>
</template>

<script>
import TheTitle from '../The-title'
import BlogCard from '../Blog-card'
import axios from 'axios'

export default {
  name: 'Home-blog',
  components: {TheTitle, BlogCard},
  data() {
    return {
      theme: 'Blog',
      title: 'Latest News',
      posts: []
    }
  },
  methods: {
    goToBlogPost(id) {
      this.$router.push({ name: 'Blog-post', params: {postId: id} })
    },
    async getAllBlogs() {
      const dataRes = await axios(`http://old.phoenixglobaltradings.com/Blog`).then(res => res.data)
      this.posts = dataRes
    }
  },
  mounted() {
    // this.getAllBlogs()
  },
}
</script>

<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

.home-blog{
  padding: 100px 0;
  // MEDIA
  @include media-size(1430px) {
    padding: 50px 0;
  }
  
  .blog-wrapper{
    margin-top: 70px;
    display: flex;
    justify-content: space-between;
    gap: 30px 0;
    // MEDIA
    @include media-size(1430px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    @include media-size(1230px) {
      margin-top: 50px;
    }
  }
  
  .blog-btn{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-top: 50px;
    
    .link{
      font-family: 'Montserrat';
      font-weight: 600;
      color: #1D68BF;
      padding: 23px 84px;
      border-radius: 50px;
      background: #FFFFFF;
      box-shadow: 0 7px 50px rgba(29, 105, 191, 0.5);
      transition: 0.3s;
      // MEDIA
      @include media-size(790px) {
        font-size: 16px;
        padding: 17px 60px;
      }
      
      &:hover{
        transform: translateY(-7px);
