<template>
  <div class="contact-form-popup">

    <!-- FORM -->
    <form @submit.prevent="sendMail">
      <input v-model="messageObg.email" type="text" class="input" placeholder="Enter your email...">
      <textarea v-model="messageObg.message" class="input" placeholder="Enter your comments..."></textarea>
      <div class="button">
        <p
          class="text-message"
          v-show="messageTextStatus"
          :class="messageTextColor"
        >{{messageText}}</p>
        <button v-show="!messageSent">Send</button>
      </div>
    </form>
    
    <div class="text">
      <!-- <span>Contact us</span> -->
      <h2>Contact with us</h2>
      <p>Our offices: <br>
            USA: 1321 Upland Dr. PMB 792; Houston, TX; 77043-4718 <br>
            UK: 86-90 Paul street, EC2A 4NE, London <br>
            Turkey: Kolektif House, Maslak 42 – Istanbul <br>
      </p>
      
      <div class="social">
        <a href="https://www.facebook.com/Phoenix-global-trading-105727821855505"><i class="fab fa-facebook"></i></a>
        <a href="https://t.me/phoenixestate"><i class="fab fa-telegram"></i></a>
        <a href="https://www.linkedin.com/in/phoenix-global-trade-3246a2220"><i class="fab fa-linkedin-in"></i></a>
        <a href="https://twitter.com/Phoenixglobalt1"><i class="fab fa-twitter"></i></a>
        <a href="https://instagram.com/phoenix_global_trading?utm_medium=copy_link"><i class="fab fa-instagram"></i></a>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Contact-form-popup",
  data() {
    return {
      messageText: 'Fill in all the fields',
      messageTextStatus: false,
      messageTextColor: 'error',
      messageSent: false,
      messageObg: {
        email: '',
        message: '',
      },
      // mailToAddress: require('/assets/PHPMailer')
    }
  },
  methods: {
    sendMail() {
      if(this.messageObg.email == '' && this.messageObg.message == ''){
        this.messageText = 'Fill in all the fields',
        this.messageTextStatus = true,
        this.messageTextColor = 'error'
        setTimeout(() => this.messageTextStatus = false, 4000)
        return
      }
      axios.post('https://biolab-mail.herokuapp.com/', this.messageObg)
        .then((response) => {
          this.messageText = 'Message sent!'
          this.messageTextStatus = true
          this.messageSent = true
          this.messageTextColor = 'success'
          this.messageObg.email = ''
          this.messageObg.message = ''
          console.log(response);
        })
        .catch((error) => {
          console.log('это catch ошибка: ',error);
          this.messageObg.email = ''
          this.messageObg.message = ''
        });
    }
  }
};
</script>

<style lang="scss">
@mixin media-size($size) {
  @media (max-width: $size) {
    @content;
  }
}

.contact-form-popup {
  position: relative;
  background: linear-gradient(to right bottom, #eb2027, #ba5400, #f7cf24);
  border-radius: 20px;
  padding: 50px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ffffff;
  // MEDIA
  @include media-size(1430px) {
    padding: 30px 50px;
  }
  @include media-size(790px) {
    flex-wrap: wrap;
  }

  .text {
    max-width: 440px;
    padding-left: 30px;
    @include media-size(790px) {
      padding-left: 0;
      padding-top: 15px;
    }

    span {
      font-size: 25px;
      font-weight: 600;
      font-family: "Montserrat";
      margin-bottom: 8px;
      // MEDIA
      @include media-size(1430px) {
        font-size: 20px;
      }
      @include media-size(1230px) {
        font-size: 17px;
      }
    }

    h2 {
      font-size: 49px;
      font-weight: 600;
      font-family: "Montserrat";
      // MEDIA
      @include media-size(1430px) {
        font-size: 40px;
      }
      @include media-size(1230px) {
        font-size: 30px;
      }
    }

    p {
      font-size: 14px;
      line-height: 20px;
      margin: 8px 0 20px;
    }

    .social {
      display: flex;

      a {
        color: #ffffff;
        font-size: 30px;
        margin-right: 22px;

        .fab {
          transition: 0.3s;
        }

        &:hover .fab {
          transform: translateY(-5px);
        }
      }
    }
  }

  form {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    max-width: 800px;
    // MEDIA
    @include media-size(1430px) {
      max-width: 620px;
    }
    @include media-size(790px) {
      margin-top: 20px;
    }

    .input {
      width: 100%;
      background: #ffffff;
      border-radius: 10px;
      padding: 20px 37px;
      margin-bottom: 30px;
      border: none;
      outline: none;
      font-size: 25px;
      font-weight: 700;
      color: #555555;
      // MEDIA
      @include media-size(1430px) {
        padding: 14px 20px;
        font-size: 20px;
        margin-bottom: 20px;
      }
      @include media-size(1020px) {
        padding: 12px 17px;
        font-size: 16px;
        margin-bottom: 15px;
      }

      &::placeholder {
        color: #b9b9b9;
        font-family: "Circe";
        font-weight: 700;
      }
    }

    textarea {
      resize: none;
      margin-bottom: 0 !important;
      border-radius: 10px 10px 0 0 !important;
    }

    .button {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      width: 100%;
      background: #ffffff;
      padding: 0 25px 15px;
      border-radius: 0 0 10px 10px;
      // MEDIA
      @include media-size(1430px) {
        transform: translateY(-5px);
      }
      @include media-size(1020px) {
        padding: 0 17px 10px;
      }
      
      .text-message{
        color: red;
        font-weight: 700;
        margin: 15px;
        font-size: 20px;
        
        &.success{
          color: green;
        }
        &.error{
          color: red;
        }
      }
      
      button {
        font-size: 15px;
        font-family: "Montserrat";
        font-weight: 600;
        color: #ffffff;
        padding: 15px 50px;
        border-radius: 10px;
        background: linear-gradient(to right top, #eb2027, #ba5400, #f7cf24);
        transition: 0.5s;
        box-shadow: 0 3px 20px #ba5400;
        border: none;
        outline: none;
        cursor: pointer;
        // MEDIA
        @include media-size(1020px) {
          font-size: 14px;
          padding: 10px 25px;
        }

        &:hover {
          transform: translateY(-7px);
          box-shadow: 0 15px 50px #ba5400;
        }
      }
    }
  }
}
</style>