<template>
  <header :class="{ 'scrolled-nav' : scrolledNav }">
    <div class="container">
      <nav>
        <div class="brandng">
          <router-link :to="{name: 'Home'}">
            <img src="@/assets/images/nav-logo.png" alt="brand">
          </router-link>
        </div>
        
        <ul v-show="!mobile" class="navigation">
          <li>
            <router-link class="nav-link" :to="{name: 'About'}">About Company</router-link>
          </li>
          <li>
            <router-link class="nav-link" :to="{name: 'Catalog'}">Products</router-link>
          </li>
          <li>
            <router-link class="nav-link" :to="{name: 'Partners'}">our Partners</router-link>
          </li>
          <li>
            <router-link class="nav-link" :to="{name: 'Blog'}">News</router-link>
          </li>
          <!-- <li>
            <router-link class="nav-link" :to="{name: 'Contact'}">Contacts</router-link>
          </li>  -->
        </ul>
        
        <div class="burger-icon" v-show="mobile">
          <i 
            class="fas fa-bars"
            @click="toggleMobileNav" 
            :class="{'burger-icon-active' : mobileNav}"
            >
          </i>
        </div>
        
        <transition name="mobile-nav">
          <ul v-show="mobileNav" class="dropdown-navigation">
          <li @click="mobileNav = false">
            <router-link class="nav-link" :to="{name: 'About'}">About Company</router-link>
          </li>
          <li @click="mobileNav = false">
            <router-link class="nav-link" :to="{name: 'Catalog'}">Products</router-link>
          </li>
          <li @click="mobileNav = false">
            <router-link class="nav-link" :to="{name: 'Partners'}">Our Partners</router-link>
          </li>
          <li @click="mobileNav = false">
            <router-link class="nav-link" :to="{name: 'Blog'}">News</router-link>
          </li>
          <!-- <li>
            <router-link class="nav-link" :to="{name: 'Contact'}">Contacts</router-link>
          </li>  -->
        </ul>
        </transition>
        
      </nav>
    </div>
  </header>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      scrolledNav: null,
      mobile: null,
      mobileNav: null,
      windowWidth: null
    }
  },
  created() {
    window.addEventListener('resize', this.checkScreen)
    this.checkScreen()
  },
  methods: {
    toggleMobileNav() {
      this.mobileNav = !this.mobileNav
    },
    checkScreen() {
      this.windowWidth = window.innerWidth
      if(this.windowWidth < 790) {
        this.mobile = true
        return
      }
      this.mobile = false
      this.mobileNav = false
      return
    },
    updateScrolledNav() {
      const scrollPossition = window.scrollY
      if(scrollPossition > 50) {
        this.scrolledNav = true
        return
      }
      this.scrolledNav = false
    }
  },
  mounted() {
    window.addEventListener('scroll', this.updateScrolledNav)
  },
};
</script>


<style lang="scss">
  @mixin media-size($size){
    @media(max-width: $size){
      @content
    }
  }

  header{
    width: 100%;
    background: transparent;
    padding: 66px 0 15px;
    transition: all 0.3s;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9999;
    // MEDIA
    @include media-size(1230px){
      padding: 35px 0 15px;
    }
    
    nav{
      display: flex;
      justify-content: space-between;
      align-items: center;
      
      .brandng{
        max-width: 206px;
        transition: width 0.8s;
        
        a{
          display: flex;
          align-items: center;
          justify-content: center;
          
          img{
            width: 100%;
            transition: width 0.8s;
          }
        }
      }
      
      .navigation{
        display: flex;
        
        li{
          .nav-link{
            font-size: 16px;
            font-family: 'Montserrat';
            color: #000000;
            padding: 10px 20px;
            text-transform: uppercase;
            // MEDIA
            @include media-size(1230px){
              font-size: 14px;
              padding: 10px 15px;
            }
            @include media-size(1020px){
              font-size: 13px;
              padding: 10px 10px;
            }
          }
        }
      }
      
      .burger-icon{
        i{
          font-size: 30px;
          cursor: pointer;
          transition: all 0.8s ease;
        }
      }
      
      .burger-icon-active{
        transform: rotate(180deg);
      }
      
      .dropdown-navigation{
        position: fixed;
        left: 0;
        top: 0;
        background: #ffffff;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        max-width: 250px;
        
        li{
          padding: 25px;
          
          .nav-link{
            font-size: 20px;
            color: #000000;
          }
        }
      }
      
      .mobile-nav-enter-active,
      .mobile-nav-leave-active{
        transition: all 1s ease;
      }
      
      .mobile-nav-enter-from,
      .mobile-nav-leave-to{
        transform: translateX(-250px);
      }
      
      .mobile-nav-enter-to{
        transform: translateX(0);
      }
    }
  }
  
  .scrolled-nav{
    padding: 20px 0;
    background: #f1f1f1d8;
    // MEDIA
    @include media-size(1230px){
      background: #f1f1f1d2;
    }
    
    nav{
      .brandng{
        width: 150px;
      }
    }
  }
</style>
