<template>
  <section class="our-products">
    <div class="container">
      <the-title :theme="theme" :title="title" />
      
      <div class="product-wrapper" v-if="productsHome.length">
        <product-card 
          v-for="product in productsHome.slice(0, 4)"
          :key="product.id"
          :product_data="product"
        />
      </div>
      <div class="no-connect" v-else>
        <h1>Loading...</h1>
      </div>
    </div>
  </section>
</template>

<script>
import TheTitle from '../The-title'
import ProductCard from '../Product-card'
import axios from 'axios'

export default {
  name: 'Our-products',
  components: { TheTitle, ProductCard },
  data() {
    return {
      theme: 'Our Products',
      title: 'New products',
      productsHome: []
    }
  },
  methods: {
    async getProducts() {
      const dataRes = await axios(`http://old.phoenixglobaltradings.com/products`).then(res => res.data)
      this.productsHome = [...dataRes]
    }
  },
  mounted() {
    this.getProducts()
  },
}
</script>

<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

.our-products{
  padding-top: 60px;
  .product-wrapper{
    margin-top: 90px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 30px 0;
    // MEDIA
    @include media-size(1700px){
      justify-content: center;
      gap: 30px;
    }
  }
  
  .no-connect{
    h1{
      font-size: 80px;
      font-weight: 300;
      color: #999;
      text-align: center;
      width: 100%;
      margin-top: 50px;
      // Media
      @include media-size(1200px){
        font-size: 50px;
      }
      @include media-size(570px){
        font-size: 30px;
      }
    }
  }
}
</style>