<template>
  <div class="blog-card">
    <img :src="`http://old.phoenixglobaltradings.com/images/${post_data.img}`" alt="">
    
    <div class="text">
      <p class="content">{{ post_data.title }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Blog-card',
  props: {
    post_data: {
      type: Object,
      default: () => {}
    }
  }
}
</script>
<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

.blog-card{
  width: 320px;
  height: 400px;
  position: relative;
  border-radius: 10px;
  padding: 40px;
  background: #FFFFFF;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  cursor: pointer;
  box-shadow: 0px 30px 60px #a55f2659;
  transition: 0.5s;
  // MEDIA
  @include media-size(1700px) {
    width: 450px;
    height: 450px;
  }
  @include media-size(1430px) {
    width: 350px;
    height: 400px;
  }
  @include media-size(570px) {
    width: 100%;
    height: 400px;
    padding: 20px;
  }
  
  &:hover{
    transform: translateY(-5px);
  }
  
  img{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .text{
    position: relative;
    width: 100%;
    background: #FFFFFF;
    padding: 27px 32px;
    border-radius: 10px;
    
    
    .date{
      font-size: 12px;
      font-family: 'Montserrat';
      font-weight: 600;
      color: #979797;
      margin-bottom: 12px;
      transition: 0.2s;
    }
    
    .content{
      font-size: 14px;
      font-family: 'Montserrat';
      font-weight: 600;
      line-height: 24px;
      color: #000000;
      transition: 0.2s;
    }
  }
  
  &:hover .text{
    background: linear-gradient(to right, #eb2027, #ba5400, #f7cf24);
    
    .date{
      color: #FFFFFF;
    }
    .content{
      color: #FFFFFF;
    }
  }
}
</style>