<template>
  <div class="product-card">
    <div class="product-image">
      <img :src="`http://old.phoenixglobaltradings.com/images/${product_data.imgs[0].img}`" :alt="product_data.imgs[0].img">
    </div>
    
    <div class="product-info">
      <h3 title="" class="product-title">{{ product_data.name }}</h3>
      
      <div class="product-link">
        <router-link 
          class="link" 
          :to="{ name: 'Product-info', params: {productId: product_data._id} }"
        >
          View Product
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Product-card',
  props: {
    product_data: {
      type: Object,
      default: () => {}
    }
  }
}
</script>

<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

  .product-card{
    width: 100%;
    max-width: 380px;
    background: #FFFFFF;
    border-radius: 10px;
    padding: 30px;
    text-align: center;
    box-shadow: 6px 10px 25px #f795245b;
    // margin: 20px 10px;
    // MEDIA
    @include media-size(1700px){
      max-width: 300px;
      padding: 20px;
    }
    @include media-size(1430px){
      max-width: 320px;
      padding: 20px;
      // margin: 30px;
    }
    @include media-size(570px){
      max-width: 100%;
      // margin: 20px 0;
    }
    
    .product-image{
      width: 100%;
      height: 225px;
      overflow: hidden;
      margin-bottom: 22px;
      // MEDIA
      @include media-size(1700px){
        height: 200px;
      }
      
      img{
        height: 100%;
      }
    }
    
    .product-info{
      .product-title{
        font-size: 18px;
        font-family: 'Montserrat';
        font-weight: 600;
        color: #000000;
        height: 47px;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
        // MEDIA
        @include media-size(1700px){
          font-size: 16px;
        }
      }
      
      .product-category{
        font-size: 14px;
        color: #808080;
        margin: 10px 0;
        letter-spacing: 2px;
      }
      
      .product-link{
        display: inline-block;
        margin-top: 15px;
        
        .link{
          font-size: 15px;
          font-family: 'Montserrat';
          font-weight: 600;
          color: #ffffff;
          padding: 15px 50px;
          background: linear-gradient(to right, #eb2027, #ba5400, #f7cf24);
          transition: 0.5s;
          box-shadow: 0 3px 20px #ba5400;
          border: none;
          outline: none;
          cursor: pointer;
          // MEDIA
          @include media-size(1700px){
            font-size: 14px;
            padding: 13px 40px;
          }
        }
        
        &:hover .link{
          transform: translateY(-7px);
          box-shadow: 0 15px 50px #ba5400;
        }
      }
    }
    
  }
</style>